<template><section><h1>描述</h1>
<p>此组件用于页面有插件，需要盖过插件层级的解决方案。</p>
<h3>此组件用于需要遮盖住的区域，会生成一个容器，所以可以直接使用它替代父容器。</h3>
<p><strong>注意：组件的样式定义中必须设置定位，如 relative或 absolute</strong></p>
<p><em>modal弹出层为特殊，内容部分使用此组件时，不加定位可以覆盖住整个 modal（包括 head和  footer）</em></p>
<h1>效果展示</h1>
<template>
  <div style="position:relative">
    <object type="application/x-webplayercontrol" style="height:200px;width: 200px;"></object>
    <bs-cover class="cover-test" v-model="isCover">
      <v-btn @click="isCover=!isCover">toggle</v-btn>
      <v-btn @click="f">fullscreen</v-btn>
    </bs-cover>
  </div>
</template>


<h1>代码示例</h1>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">object</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"application/x-webplayercontrol"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"height:200px;width: 200px;"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">object</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bs-cover</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"cover-test"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"isCover"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">v-btn</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"isCover=!isCover"</span>&gt;</span>toggle<span class="hljs-tag">&lt;/<span class="hljs-name">v-btn</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">v-btn</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"f"</span>&gt;</span>fullscreen<span class="hljs-tag">&lt;/<span class="hljs-name">v-btn</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">bs-cover</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">isCover</span>: <span class="hljs-literal">true</span>
    }
  },
  <span class="hljs-attr">methods</span>: {
    f() {
      <span class="hljs-keyword">this</span>.plugin.SetPlayFullScreen()
    }
  },
  mounted() {
    <span class="hljs-keyword">this</span>.plugin = <span class="hljs-keyword">this</span>.$el.querySelector(<span class="hljs-string">'object'</span>)
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"stylus"</span> <span class="hljs-attr">scoped</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.cover-test</span>
  <span class="hljs-selector-tag">position</span> <span class="hljs-selector-tag">absolute</span>
  <span class="hljs-selector-tag">top</span> 16<span class="hljs-selector-tag">px</span>
  <span class="hljs-selector-tag">left</span> 100<span class="hljs-selector-tag">px</span>
  <span class="hljs-selector-tag">width</span> 300<span class="hljs-selector-tag">px</span>
  <span class="hljs-selector-tag">height</span> 200<span class="hljs-selector-tag">px</span>
  <span class="hljs-selector-tag">background</span> <span class="hljs-selector-tag">gray</span>
  <span class="hljs-selector-tag">text-align</span> <span class="hljs-selector-tag">right</span>
