<h1>props</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>属性名称</th>
<th>描述</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>是否遮盖插件</td>
<td>必须</td>
</tr>
</tbody>
</table>
<p><strong>此组件用 v-if的 iframe组件来实现，所以会有性能上的损耗，在不需要显示时，切记要把 value给设置为 false</strong></p>
<h1>events</h1>
<p>无</p>
</section></template>
<style lang="stylus" scoped>
.cover-test
  position absolute
  top 16px
  left 100px
  width 300px
  height 200px
  background gray
  text-align right
</style><style lang="stylus" scoped>
.cover-test
  position absolute
  top 16px
  left 100px
  width 300px
  height 200px
  background gray
  text-align right
</style>
<script>
export default {
  data () {
    return {
      isCover: true
    }
  },
  methods: {
    f() {
      this.plugin.SetPlayFullScreen()
    }
  },
  mounted() {
    this.plugin = this.$el.querySelector('object')
  }
}
</script>


